import React, { Suspense, lazy } from "react"
import SEO from "../components/seo"

const OtherComponent = lazy(() => import('../components/game'))

const Demo = () => {
  return (
    <>
      <SEO title="Demo" />
      <Suspense fallback={<div>Loading...</div>}>
        <OtherComponent />
      </Suspense>
    </>
  )
}

export default Demo